
@media (max-width: 525px) {
    
    #top-welcum div{
        text-align: center;
        font-size: 27px;
      }
      #top-containter{
        box-shadow: none;
      }
      #middle-form input{
        font-size: 15px;
      }
      #middle-form button{
        display: none;
      }

      #ab-title-container{
        font-size: 38px;
      }
      #about-us-container{
        padding: 0px;
      }
      #containened-components{
        display: grid;
        grid-template-columns: auto;
        width: fit-content;
        margin: auto;
      }
      #ab-cta{
        margin-top: 10px;
        text-align: center;
        display: grid;
        grid-template-columns: auto;
        grid-gap: 0px;
        margin-left: 0px;
        background-color: transparent;
        border-radius: 20px;
        padding-top: 20px;
        padding-bottom: 20px;
      }
      #ab-cta button{
        width: fit-content;
        letter-spacing: 0.5px;
        letter-spacing: 0px;
        font-size: 14x;
        margin-left: 0px;
        color: #00d9ff;
        margin: auto;
        margin-bottom: 5px;
      }
      #ab-cta button:hover{
        background-color: transparent;
        color: aqua;
      }
      #ab-cta button:focus{
        background-color: transparent;
        color: aquamarine;
        font-weight: normal;
      }


        /* about us */
        
      #abs-content{
        background-color: var(--superblue);
        font-weight: bolder;
        height: fit-content;
        padding-bottom: 30px;
      }
      
      #abs-cta{
        overflow: scroll;
        margin: auto;
        margin-bottom: 10px;
        padding: 3px;
        background-color: transparent;
        border-radius: 0px;
        text-align: center;
      }
      
      #abs-cta button{
        border-radius: 50px;
        background-color: var(--blacker);
        letter-spacing: 1.5px;
        color: white;
        letter-spacing: 0.5px;
        font-size: 16px;
      }
      #abs-cta button:hover{
        background-color: white;
        font-weight: bold;
        color: black;
      }
      #some-ab-info, #abs-contentlong {
        background-color: var(--blacker);
        color: var(--white);
        font-weight: normal;
        padding: 3%;
        width: 80%;
        margin: auto;
        height: 250px;
        text-align: center;
        font-style: italic;
        font-size: 16px;
        letter-spacing: 1.5px;
        border-radius: 15px;
        color: white;
        white-space: normal;
        overflow: scroll;
        border-bottom: 20px solid var(--blacker);
        border-top: 5px solid var(--blacker);
      }

      #some-ab-info{
        background-color: transparent;
        height: fit-content;
        margin-top: 50px;
        border: none;
        font-size: 14px;
        font-style: normal;
      }
      #ab-pad{
        padding: 10px;
      }
      #ab-pad div{
        padding: 0px;
        color: white;
      }
      #rocket{
        background-color: blue;
        border-bottom: 5px solid aquamarine;
        height: 4%;
      }
      #rocket-container{
        margin-top: -60px;
        height: 100px;
        width: 100px;
      }

      #g-container{
        padding: 20px;
        display: flex;
        flex-direction: row;
        grid-gap: 5px;
        height: fit-content;
        overflow: scroll;
        white-space: nowrap;
        background-color: transparent;
        padding-bottom: 0px;
        border-radius: 0px;
      }

      #guides{
        font-size: 15px;
        padding: 20px;
        border: 1px solid var(--black);
        border-radius: 7px;
        margin: 5px;
        color: white;
        box-shadow: 0 0 3px var(--superblue);
        height: fit-content;
      }

      #image-video {
        margin: auto;
        width: 240px;
        height: 150px;
        padding: 10px;
        border-radius: 5px;
        background-color: var(--blacker);
        margin-bottom: 10px;
        
        display: flex;
        align-items: center;  
        text-align: center;
        justify-content: center; 
      }
      #g-maincontainer{
        padding: 0px;
        background-color: var(--white);
      }
}

@media (max-width: 575px) {
    #rocket{
        margin-top: -130px;
    }
}

@media (max-width: 435px) {
    #rocket{
        margin-top: -230px;
    }
}
@media (max-width: 380px) {
    #rocket{
        margin-top: -325px;
    }

    #g-container{
      padding: 20px;
      display: grid;
      /* flex-direction: row; */
      grid-template-columns: auto auto;
      grid-gap: 0px;
      height: 500px;
      overflow: scroll;
      white-space: nowrap;
      background-color: transparent;
      padding-bottom: 0px;
      border-radius: 0px;
      height: 490px;
      border-bottom: 20px solid white;
      
    }

    #guides{
      font-size: 15px;
      padding: 0px;
      border: 1px solid var(--black);
      border-radius: 7px;
      margin: 5px;
      color: white;
      box-shadow: 0 0 3px var(--superblue);
      height: fit-content;
      width: fit-content;
      padding-bottom: 10px;
    }

    #image-video {
      margin: auto;
      width: 137px;
      height: 80px;
      padding: 10px;
      border-radius: 5px;
      background-color: var(--blacker);
      margin-bottom: 10px;
      display: flex;
      align-items: center;  
      text-align: center;
      justify-content: center; 
    }
    #g-maincontainer{
      padding: 0px;
    }
    #user-cat, #contents{
      display: none;
    }

    #g-header{
      margin-top: -2%;
      text-align: center;
      white-space: normal;
    }
    #g-header span{
      margin-top: -2%;
      font-size: 14px;
      margin-right: 8px;
      font-family: 'Lato', sans-serif;
    }
    
    #g-title{
      text-align: center;
      color: greenyellow;
      text-decoration: underline;
      cursor: pointer;
    }
}

@media (max-width: 350px) {
    #rocket{
        margin-top: -460px;
    }

    #image-video {
      margin: auto;
      width: 110px;
      margin-bottom: 5px;
    }
}