@import url('https://fonts.googleapis.com/css2?family=DM+Sans&family=Rubik:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=DM+Sans&family=Lato&family=Rubik:wght@300&display=swap');

::-webkit-scrollbar {
  width: 0;
  height: 0;
  display: none;
}

:root {
  --green: #76D7C4;
  --black: #151E30;
  --white: #FFFFFF;
  --gray: #CDD0CF;
  --blacker: #0F141E;
  --brighter: #1D263A;
  --superblue: #03050f;
}
#blue{
  color: var(--white);
}

#g-header{
  text-align: justify;
  white-space: normal;
}
#g-header span{
  font-size: 22px;
  margin-right: 8px;
  font-family: 'Lato', sans-serif;
}

#g-title{
  color: #00d9ff;
  text-decoration: none;
  cursor: pointer;
}
#g-title:hover{
  color: white;
}
#g-header div:nth-child(2) span{
  font-size: 15px;
  background-color: orange;
  padding: 5px 10px;
  text-transform: capitalize;
  border-radius: 3px;
  color: var(--superblue);
  cursor: pointer;
}
#g-header div:nth-child(2) span:hover{
  text-decoration: underline;
  color: #03050f;
  font-weight: bold;
}
#g-header div:nth-child(2) span:nth-child(3){
  background-color: aqua;
  border-radius: 50px;
}
#g-header div:nth-child(2) span:nth-child(2){
  text-decoration: none;
  cursor:default;
  background-color: var(--blacker);
  color: white;
}
#g-header div:nth-child(2) span:nth-child(2):hover{
  text-decoration: none;
  cursor:default;
  background-color: var(--blacker);
  color: white;
}

#user-cat{
  padding: 5px 0px 5px 0px;
}
#top-containter{
  background-color: var(--superblue);
  padding: 40px;
  box-shadow: 0 0 10px var(--blacker);
}
body{
  font-family: 'Rubik', sans-serif;
  font-weight: bold;
  font-size: 19px;
  padding: 0px;
  margin: 0px;
  background-color: var(--superblue);
}

#container{
  padding: 25px;
  background-color: var(--superblue);
  margin: 20px;
  width: fit-content;
  margin: auto;
  border-radius: 10px;
}

#micheal{
  background-color: var(--brighter);
  color: white;
  font-weight: normal;
  font-style: italic;
  border-radius: 50px;
  position: absolute;
}

button{
  font-family: 'Rubik', sans-serif;
  font-weight: bold;
  font-size: 17px;
  border: none;
  margin: 3px;
  width: fit-content;
  padding: 8px 15px;
  border-radius: 10px; 
  cursor: pointer;
}

/*home*/
#middle-form{
  align-items: center;
  text-align: center;
  width:  fit-content;
  margin: auto;
  background-color: var(--blacker);
  padding: 10px 18px;
  padding-left: 13px;
  border-radius: 100px;
  padding-right: 13px;
}

#top-welcum div{
  text-align: center;
  font-size: 50px;
  padding: 30px;
  font-family: Arial, Helvetica, sans-serif;
  color: var(--green);
  font-weight: bolder;
}
#middle-form input{
  font-size: 19px;
  font-family: 'Rubik', sans-serif;
  padding: 10px 20px;
  background-color: var(--black);
  border-radius: 50px;
  outline: none;
  border: none;
  color: var(--white);
}
#middle-form input::placeholder{
  font-family: 'Rubik', sans-serif;
  color: var(--white)
}

#middle-form button{
  background-color: transparent;
  color: aqua;
  font-family: 'Lato', sans-serif;
}

#g-maincontainer{
  padding: 40px;
  background-color: var(--blacker);
}
#g-navigationcomponent{
  padding-top: 75px;
  padding-bottom: 30px;
}

#cta-home{
  text-align: center;
  width: 100%;
  margin-top: 20px;
  background-color: var(--blacker);
  width: fit-content;
  align-self: center;
  position: absolute;
  
}
#cta-home button{
  border-radius: 4px;
  letter-spacing: 0px;
  font-size: 16px;
  background-color: var(--black);
  color: #00d9ff;
}

#g-container{
  background-color: var(--superblue);
  padding: 20px;
  border-radius: 10px;
  display: grid;
  /* flex-direction: row; */
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 5px;
  height: 900px;
  overflow: scroll;
  white-space: nowrap;
}
 
#guides{
  width: 370px;
  padding: 20px;
  border: 1px solid var(--black);
  border-radius: 7px;
  background-color: var(--superblue);
  margin: 5px;
  color: white;
  box-shadow: 0 0 3px var(--superblue);
}


#image-video {
  margin: auto;
  height: 150px;
  padding: 10px;
  border-radius: 5px;
  background-color: var(--blacker);
  margin-bottom: 10px;
  
  display: flex;
  align-items: center;  
  text-align: center;
  justify-content: center; 
}

#contents{
  text-align: justify;
  white-space: normal;
  height: 100px;
  overflow: scroll;
  margin-top: 10px;
  background-color: #0d0213;
  cursor: pointer;
  border-radius: 5px;
  padding: 10px;
  font-weight: normal;
}
 
#ab-title-container{
  font-size: 50px;
  font-family: Arial, Helvetica, sans-serif;
  color: #00d9ff;
  font-weight: bolder;
  text-align: center;
  cursor: pointer;
  width: fit-content;
  margin: auto;
}
#ab-title-container:hover{
  text-decoration: underline;
}

#about-us-container{
  background-color: transparent;
  padding: 10px;
}
#under-container{
  background-color: var(--superblue);
  padding-bottom: 30px;
}

#ab-cta{
  text-align: center;
  display: grid;
  grid-template-columns: auto;
  grid-gap: 0px;
  margin-left: 10px;
}
#ab-cta button{
  background-color: var(--blacker);
  color: #00d9ff;
  letter-spacing: 0.5px;
  border-top-right-radius: 50px;
  border-bottom-right-radius: 50px;
  border-top-left-radius: 30px;
  
}
#ab-cta button:hover{
  background-color: #00d9ff;
  color: var(--superblue);
}
#ab-cta button:focus{
  background-color: #00d9ff;
  color: var(--superblue);
  font-weight: bolder;
}

#ab-pad{
  padding: 40px;
}
#ab-pad div{
  color: #00d9ff;
}

#puppy{
  height: 200px;
  width: 200px;
  margin: auto;
}
#puppy-container{
  height: 200px;
  width: 200px;
  background-color: var(--black);
  padding: 20px;
  border-radius: 20px;
  border-top-right-radius: 100px;

}
#external-puppy{
  background-color: var(--blacker);
  margin-top: -12%;
  margin-left: 1%;
  border-radius: 20px;
  width: fit-content;
  border-bottom-left-radius: 100px;
  margin: auto;
}
#containened-components{
  display: grid;
  grid-template-columns: auto auto;
  width: fit-content;
  margin: auto;
}

#abs-content{
  background-color: var(--superblue);
  font-weight: bolder;
  height: 1000px;
}

#abs-cta{
  width: fit-content;
  margin: auto;
  margin-bottom: 10px;
  padding: 3px;
  border-radius: 50px;
}

#abs-cta button{
  border-radius: 50px;
  background-color: var(--black);
  letter-spacing: 1.5px;
  color: white;
}
#abs-cta button:hover{
  background-color: white;
  font-weight: bold;
  color: black;
}
#some-ab-info, #abs-contentlong {
  background-color: var(--blacker);
  color: var(--white);
  padding: 3%;
  width: 70%;
  margin: auto;
  text-align: center;
  font-style: italic;
  font-size: 18px;
  letter-spacing: 1.5px;
  border-radius: 20px;
  color: white;
}

#some-ab-info{
  background-color: transparent;
  margin-top: 50px;
  font-style: normal;
  font-weight: normal;
}

#rocket{
  position: absolute;
  background-color:transparent;
  padding: 0px;
  border-radius: 20px;
  margin-top: -2%;
  margin-left: -6%;
}
#rocket-container{
  width: 160px;
  height: 160px;
  margin: auto;
}

 



